export default {
	data() {
		return {
			address:'',




			navBar: false

		};
	},
	methods: {
		LeftClick(){
			uni.navigateBack({
				delta:1
			})
		},
		mapPage() {
		
			let that=this;
	   
			uni.chooseLocation({
				success:function(res){
					that.address=res.address;
				},
				fail:function(err){
					console.log(err)
				}
			});
		}
	}
};
